import { Component, h, Prop, Host, FunctionalComponent, Event, EventEmitter } from '@stencil/core';
import Close from '@material-design-icons/svg/round/close.svg';
import { FileUtils } from '../../../utils/data/file';

@Component({
  tag: 'rwc-tag',
  styleUrl: 'tag.scss',
  shadow: true
})
export class RwcTag {
  /**
   * Path to the icon.
   */
  @Prop() icon: string = '';

  /**
   * Render icon.
   */
  @Prop() renderIcon: boolean = false;

  /**
   * Set tag selected.
   */
  @Prop() selected: boolean = false;

  /**
   * Set tag to clickable.
   */
  @Prop() clickable: boolean = false;

  /**
   * Event fired when the delete icon is clicked.
   */
  @Event({ eventName: 'deleteTag ' }) deleteTagEvent: EventEmitter<void>;

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div
          class={ {
            container: true,
            clickable: this.clickable,
            selected: this.selected
          } }
        >
          <div class="text">
            <slot></slot>
          </div>
          { this.renderIconWrapper() }
        </div>
      </Host>
    );
  }

  /**
   * Fires the deleteTag event.
   */
  private deleteTag(): void {
    this.deleteTagEvent.emit();
  }

  /**
   * Renders the icon wrapper.
   */
  private renderIconWrapper(): FunctionalComponent {
    if (this.renderIcon) {
      return (
        <div class="icon-wrapper"
          onClick={ () => this.deleteTag() }
        >
          <slot name="icon">
            { this.renderIconElement() }
          </slot>
        </div>
      )
    }
  }

  /**
   * Renders the icon element.
   */
  private renderIconElement(): FunctionalComponent {
    if (this.icon) {
      return (
        <img class="icon" src={ this.icon } alt={ FileUtils.getCleanFileName(this.icon) }></img>
      )
    }
    return (
      <div innerHTML={ Close } class="icon svg-icon"></div>
    )
  }
}
