@import 'global.scss';

:host {
  /**
  * @prop --tag-clickable-background-color: The background color of clickable tag.
  * @prop --tag-clickable-hover-background-color: The background color of clickable tag on hover.
  * @prop --tag-clickable-active-background-color: The backgrund color of clickable tag on mouse down.
  * @prop --tag-border: The border of the tag.
  * @prop --tag-font-size: The font size of the tag.
  * @prop --tag-hover-text-color: The text color of the tag on hover.
  * @prop --tag-border-radius: The border radius of the tag.
  * @prop --tag-padding: The top-bottom and left-right padding for tag.
  * @prop --tag-box-shadow: The shadow of the tag.
  * @prop --tag-icon-size: The size of the icon of the tag.
  * @prop --tag-width: The width of the tag.
  * @prop --tag-background-color: The background color of the tag.
  * @prop --tag-text-color: The text color of the tag.
  * @prop --tag-transition-duration: The duration of transition on box shadow of the tag.
  * @prop display: The display mode of the host element.
  */
  --tag-border: 2px solid #{$rwc-color-green-300};
  --tag-border-radius: #{$rwc-spacing--xl};

  --tag-box-shadow: #{$rwc-box-shadow-md};

  --tag-background-color: #{$rwc-color-green-300};

  --tag-clickable-background-color: #{$rwc-color-foundation-white};
  --tag-clickable-hover-background-color: #{rgba($rwc-color-green-300, 0.06)};
  --tag-clickable-active-background-color: #{rgba($rwc-color-green-300, 0.10)};

  --tag-font-size: #{$rwc-font-size-label-md};
  --tag-icon-size: #{$rwc-font-size-body-md};
  --tag-icon-margin: 0 0 0 #{$rwc-spacing--2xs};
  --tag-hover-text-color: #{$rwc-color-green-300};
  --tag-text-color: #{$rwc-color-text-primary};

  --tag-padding: #{$rwc-spacing--2xs} #{$rwc-spacing--xs};
  --tag-width: fit-content;

  --tag-transition-duration: 200ms;

  display: block;
}

.container {
  border: var(--tag-border);
  align-items: center;
  border-radius: var(--tag-border-radius);
  display: flex;
  width: var(--tag-width);
  padding: var(--tag-padding);
  background-color: var(--tag-background-color);
  color: var(--tag-text-color);
  font-size: var(--tag-font-size);
}

.clickable {
  color: var(--tag-border);
  transition: box-shadow var(--tag-transition-duration) ease;
  background-color: var(--tag-clickable-background-color);

  &:hover {
    background-color: var(--tag-clickable-hover-background-color);
    box-shadow: var(--tag-box-shadow);
    color: var(--tag-hover-text-color);
    cursor: pointer;

    .icon-wrapper {
      svg {
        path {
          fill: var(--tag-hover-text-color);
        }
      }
    }
  }

  &:active {
    background-color: var(--tag-clickable-active-background-color);
  }

  &.selected {
    background-color: var(--tag-background-color);
    color: var(--tag-text-color);
    cursor: pointer;

    .icon-wrapper {
      svg {
        path {
          fill: var(--tag-text-color);
        }
      }
    }
  }

  .icon-wrapper {
    svg {
      path {
        fill: var(--tag-text-color);
      }
    }
  }
}

.icon-wrapper {
  align-self: center;
  flex-shrink: 0;
  display: flex;
  cursor: pointer;
  margin: var(--tag-icon-margin);

  svg {
    height: 100%;
    width: 100%;

    path {
      fill: var(--tag-text-color);
    }
  }
}

.icon {
  height: var(--tag-icon-size);
  width: var(--tag-icon-size);
}
